import { useEffect, useLayoutEffect, useState } from 'react';
import { subscribe } from './triggerToast';
import manageToasts from './manageToasts';
import Toast from './Toast';

export default function Toaster({
  position = 'top-center',
  richColors = false,
  gap = 8,
  edgeOffset = 16,
  isDark = window.matchMedia('(prefers-color-scheme: dark)').matches,
  animateTransformOrigin = true,
}) {
  const [toasts, setToasts] = useState([]);

  useEffect(() => {
    const unsub = subscribe((toast) => manageToasts(toast, setToasts));
    return unsub;
  }, []);

  useLayoutEffect(() => {
    const grouped = toasts.reduce((prev, t) => {
      const pos = t.position || position || 'top-center';
      (prev[pos] ||= []).push(t);
      return prev;
    }, {});

    let hasChanged = false;

    const updated = toasts.map((toast) => {
      const pos = toast.position || position || 'top-center';
      const group = grouped[pos];
      const index = group.findIndex((t) => t.id === toast.id);

      const distance = group
        .slice(0, index)
        .reduce((acc, t) => acc + (t.height || 0), 0);

      if (distance !== toast.distance) {
        hasChanged = true;
      }

      return { ...toast, distance };
    });

    if (hasChanged) {
      setToasts(updated);
    }
  }, [toasts, position]);

  return (
    <div
      style={{
        position: 'fixed',
        inset: 0,
        zIndex: 2147483647,
        pointerEvents: 'none',
        display: 'grid',
        padding: edgeOffset != null ? edgeOffset : 16,
      }}
      className={`kitzo-toast-container ${richColors ? 'kitzo-toast-rich-colors' : ''} ${isDark ? 'kitzo-toast-dark' : ''}`}
    >
      <div
        style={{
          position: 'relative',
        }}
      >
        {toasts.map((t) => (
          <Toast
            key={t.id}
            t={t}
            gap={gap}
            setToasts={setToasts}
            animateTransformOrigin={animateTransformOrigin}
            containerPosition={position}
          />
        ))}
      </div>
    </div>
  );
}
